//dragBl
function dragBl() {

  const slider = document.querySelector('.drag_bl1');

  function blDragStart() {
    setTimeout(() => slider.classList.add('active'), 100);
    window.ondragstart = function () { return false; }
    slider.style.cursor = '-webkit-grabbing';
  }
  function blDragStop() {
    slider.classList.remove('active');
    window.ondragstart = function () { return true; }
    slider.style.cursor = '-webkit-grab';
  }

  slider.style.cursor = '-webkit-grab';
  slider.addEventListener('mousedown', () => blDragStart(), true);
  window.addEventListener('mouseup', () => blDragStop(), true);

  const sliderImpetus = new Impetus({
    source: slider,
    boundX: [0, slider.scrollWidth - slider.clientWidth],
    multiplier: - 1,
    update(x) {
      this.scrollLeft = x
    }
  });

  if ($('.participation_page').length === 1) {
    let dragOffsetLeft = $('.participation_page .drag_bl').offset().left;
    $('#key_event .drag_bl').css('width', 'calc(100% + ' + dragOffsetLeft + 'px)');
  }

}
if ($('.home').length === 1 || $('.participation_page').length === 1) {
  if ($(window).width() > 991) {
    dragBl();
  }
}

