// techno-page vertical-scroll
function vertSlider(textBlockClassName, imageBlockClassName) {

  // define elements
  const txtDiv = document.querySelectorAll(`.${textBlockClassName}`);
  const imgDiv = document.querySelectorAll(`.${imageBlockClassName}`);

  // setup scroll event
  window.onscroll = () => {
    // init index
    let ind = imgDiv.length;

    // Check what txt element is visible
    txtDiv.forEach(elem => {
      if (elem.getBoundingClientRect().y > 0) ind--;
    });

    // Setup upper bound for index
    ind = Math.min(ind, imgDiv.length - 1);

    // Hide images
    imgDiv.forEach(elem => {
      elem.style.opacity = 0;
    });

    // Show img, based on txt element visible
    imgDiv[ind].style.opacity = 1;

  };
}
if (document.querySelector('.content-mask')) {
  vertSlider('mask-text__item', 'mask-img-item');
}

/* YEAR timeline scroll left/right*/
(() => {
  function scrollTimeline() {
    const btns = document.querySelectorAll('button[data-year]');
    const selectedBtn = document.querySelector('.timeline-button--current');
    const line = document.querySelector('.timeline-button');
    const scrollable = document.querySelector('.timeline-button__inner');

    let lineWidth = line.getBoundingClientRect().width;
    let btnWidth = selectedBtn.getBoundingClientRect().width;
    let space = (lineWidth * 0.9 - 3 * btnWidth) / 2;

    const offset = lineWidth * 0.05;
    let n = 0;

    btns.forEach((el, i) => {
      if (el === selectedBtn) {
        if (i > 1 && i < btns.length - 2) {
          n = i - 1;
        }
        else if (i < 2) {
          n = 0;
        }
        else {
          n = btns.length - 3;
        }
      }
    });
    scrollable.style.transform = `translate(${offset - (btnWidth + space) * n}px)`;

    btns.forEach((el, i) => {
      el.style.marginRight = `${space}px`;

      el.onclick = () => {
        const selected = document.querySelector('.timeline-button--current');
        if (selected !== el) {
          selected.classList.remove('timeline-button--current');
          selected.classList.add('timeline-button');
          el.classList.add('timeline-button--current');
        }

        const curYear = +selected.getAttribute('data-year');
        const newYear = +el.getAttribute('data-year');

        const matr = new WebKitCSSMatrix(window.getComputedStyle(scrollable).transform);
        const curTranslate = matr['e'];

        if (newYear > curYear && i < btns.length - 1 && i > 1) {
          scrollable.style.transform = `translate(${curTranslate - (btnWidth + space)}px)`;
        }
        else if (newYear < curYear && i > 0 && i < btns.length - 2) {
          scrollable.style.transform = `translate(${curTranslate + (btnWidth + space)}px)`;
        };

        const articles = document.querySelectorAll('article');
        const curArticle = document.querySelector('article.article--current');
        curArticle.classList.remove('article--current');
        articles[i].classList.add('article--current');

      };
    });
  };

  if (document.body.querySelector("#timeline")) {

    scrollTimeline();
    window.onresize = scrollTimeline;
  }

})();

